.mobile-menu-container {
  position: fixed;
  display: none;
  justify-content: flex-end;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.25);
  transition: all 1s;
  z-index: 25;
}

.mobile-menu-options {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  list-style: none;
  width: 100%;
  height: 100%;
  color: #fff;
  cursor: pointer;
  padding: 20px;
  font-size: 13px;
}

.mobile-menu-options:focus {
  outline-style: none;
  box-shadow: none;
  border-color: transparent;
  background-color: none;
}

.mobile-menu-options > li {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mobile-menu-option {
  margin: 20px 0;
}

.mobile-menu {
  display: none;
  position: fixed;
  width: 400px;
  max-width: 70%;
  height: 100vh;
  max-height: 100vh;
  background-color: #212121;
  z-index: 30;
  top: 0;
  right: 0;
  transform: translate(100%, 0);
  transition: all 1s;
}

.active-mobile-menu {
  display: none;
  position: fixed;
  width: 400px;
  max-width: 70%;
  height: 100vh;
  max-height: 100vh;
  background-color: #212121;
  z-index: 30;
  top: 0;
  right: 0;
  transition: all 1s;
  transform: translate(0, 0);
}

.mobile-menu-option-toggle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 25px;
  border-radius: 40px;
  background-color: #424242;
  font-size: 10px;
}

.mobile-menu-option-toggle-item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 35px;
}

.mobile-menu-option-toggle-api {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60%;
  height: 25px;
  background-color: #757575;
  border-radius: 40px;
  color: #fff;
}

.mobile-menu-cancel {
  position: absolute;
  top: 20px;
  right: 25px;
  font-size: 35px;
  color: #fff;
  cursor: pointer;
}

.mobile-menu-option-create {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 250px;
  height: 60px;
  border-radius: 5px;
  background-color: var(--primary-color);
  font-size: 15px;
}

@media screen and (max-width: 1200px) {
  .mobile-menu,
  .active-mobile-menu {
    display: block;
  }

  .mobile-menu-container {
    display: flex;
  }
}
